<template>
  <div id="quiz">
    <head-top title="随堂测验"></head-top>
    <van-tabs v-model="headTopActive">
      <van-tab title="截屏提问">
        <van-pull-refresh v-model="isLoading" @refresh="getQuestionData" class="pull_fresh">
          <div class="question-content" v-if="questionList.length>0">
            <div
              class="item"
              @click="selectQuestion(item)"
              v-for="(item, k) in questionList"
              :key="k"
            >
              <div class="bg fL">
                <img :src="item.img" />
              </div>
              <div class="details fR">
                <div
                  class="info"
                >{{item.SSSerial + '.[' + getQuestionTypeName(item.SSType) + '] ' + item.SSScore + '分'}}</div>
                <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
              </div>
              <span
                class="prompt"
                :class="item.SSPattern=== 3 ? 'red':'green'"
              >{{getPatternName(item.SSPattern)}}</span>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
      <van-tab title="随堂练习">
        <van-pull-refresh v-model="isLoading" @refresh="getPracticeData" class="pull_fresh">
          <div class="practice-content" v-if="practiceList.length>0">
            <div
              class="item"
              @click="selectPractice(item)"
              v-for="(item, index) in practiceList"
              :key="index"
            >
              <div class="biaoti">
                {{item["PLTitle"]}}
                <div
                  class="line_state"
                  :class="[3,4].indexOf(item['State'])>-1 ? 'red':(item['State']===2?'green':'')"
                >{{getStateName(item["State"])}}</div>
              </div>
              <div class="content">
                <div>
                  总分:
                  <span>{{item["SubjectScore"]}}</span>
                </div>
                <div>
                  题数:
                  <span>{{item["SubjectTotal"]||0}}题</span>
                </div>
                <div>
                  时间:
                  <span>{{item['CreateTime'] | formatDate(item['CreateTime']) }}</span>
                </div>
              </div>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Tab, Tabs } from "vant";

import {
  screenQuestionTypeName,
  screenPatternName,
  ClassPracticeStateName,
  ModulePicture
} from "@/utils/enum";
export default {
  name: "",
  components: { [Tab.name]: Tab, [Tabs.name]: Tabs, headTop },
  props: {},
  data() {
    return {
      headTopActive: 0,
      isLoading: false,
      questionList: [],
      practiceList: []
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (from.path.indexOf("/classPractice") > -1) {
        vm.headTopActive = 1;
      }
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.getQuestionData();
      this.getPracticeData();
    });
  },
  methods: {
    getQuestionData() {
      this.isLoading = true;
      this.questionList = [];
      this.$gatewayHttp.screenQustion
        .getList({
          courseID: this.$store.getters.course["CourseID"]
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.questionList.push({
                ...item,
                img: this.$gatewayHttp.resourse.picture(
                  item["SSContent"],
                  ModulePicture.ScreenSubject
                )
              });
            });
            // 默认自带排序了
            // this.questionList.sort((a, b) => {
            //   return parseInt(a["SSSerial"]) - parseInt(b["SSSerial"]);
            // });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    getPracticeData() {
      this.isLoading = true;
      this.practiceList = [];
      this.$gatewayHttp.classPractise
        .getList({
          courseID: this.$store.getters.course["CourseID"]
        })
        .then(res => {
          this.practiceList = [];
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              if (this.practiceList.indexOf(item) === -1)
                this.practiceList.push(item);
            });
            // this.practiceList.sort(r => r["CreateTime"]).reverse();
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    getQuestionTypeName(val) {
      return screenQuestionTypeName(val);
    },
    getPatternName(val) {
      return screenPatternName(val);
    },
    getStateName(val) {
      return ClassPracticeStateName(val);
    },
    selectQuestion(item) {
      this.$router.push("/screenQuestion/" + item["SSID"]);
    },
    selectPractice(item) {
      if (item["State"] === 1) {
        return;
      }
      this.$router.push(
        "/classPractice/" +
          item["CPID"] +
          "/" +
          item["PLID"] +
          "/" +
          item["SubjectTotal"] +
          "/" +
          item["State"]
      );
    }
  },
  computed: {},
  watch: {
    "$store.getters.classPractice"(newVal, oldVal) {
      if (newVal !== oldVal && newVal.step === 0) {
        this.headTopActive = 1;
        this.getPracticeData();
      }
    }
  }
};
</script>

<style lang='less' scoped>
#quiz {
  .question-content {
    margin-top: 10px;
    width: 100%;
    padding-bottom: 2vh;
    background: rgba(255, 255, 255, 1);
    height: calc(100vh - 4.5rem - 2vh - 10px - 44px);
    overflow-y: auto;
    .item {
      height: 12vh;
      width: 93vw;
      margin: 0 auto;
      overflow: hidden;
      position: relative;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      .bg {
        width: 30vw;
        height: 9vh;
        margin: 1vh;
        margin-left: 0px;
        text-align: center;
        img {
          max-width: 30vw;
          max-height: 9vh;
        }
      }
      .details {
        text-align: left;
        width: 53vw;
        height: 10vh;
        margin-top: 1vh;
        padding-left: 5vw;
      }
      .info {
        font-size: 1.4rem;
        font-family: MicrosoftYaHei;
        color: rgba(51, 51, 51, 1);
        line-height: 1.5;
        margin-bottom: 3vh;
      }
      .time {
        font-size: 1.2rem;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
      }
      .prompt {
        position: absolute;
        top: 1vh;
        right: 0;
      }
      .red {
        color: #e97b7b;
      }
      .green {
        color: #00cbab;
      }
    }
  }
  .practice-content {
    text-align: left;
    width: 100vw;
    // height: 34vh;
    padding-bottom: 2vh;
    margin-top: 2.2vh;

    background: rgba(255, 255, 255, 1);
    .item {
      overflow: hidden;
      width: 92vw;
      margin: 0 auto;
      // padding-bottom: 1rem;
      border-bottom: 1px solid #ddd;
      .biaoti {
        font-size: 1.4rem;
        line-height: 2.5;
        font-family: MicrosoftYaHei;
        color: rgba(51, 51, 51, 1);

        .line_state {
          text-align: right;
          float: right;
        }
        .red {
          color: #e97b7b;
        }
        .green {
          color: #00cbab;
        }
      }
      .content {
        font-size: 1.2rem;
        line-height: 2.5;
        display: flex;
        justify-content: space-between;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 44px - 15px);
    }
  }

  .rightBtn {
    .van-button--primary {
      position: absolute;
      right: 15px;
      top: 15px;
      padding: 5px;
      font-size: 1.2rem;
      width: 50px;
      height: 20px;
      background: rgba(0, 203, 171, 1);
      line-height: 12px;
      border-radius: 3px;
      color: #fff;
      border: 1px solid #00cbab;
    }
    .van-button--info {
      position: absolute;
      right: 15px;
      top: 15px;
      padding: 5px;
      font-size: 1.2rem;
      width: 50px;
      height: 20px;
      background: blue;
      line-height: 12px;
      border-radius: 3px;
      color: #fff;
      border: 1px solid #00cbab;
    }
  }
  .van-tab span {
    font-size: 1.17rem;
    font-family: MicrosoftYaHei;
    font-weight: 400;
    color: rgba(0, 203, 171, 1);
  }
  .van-tabs__line {
    width: 60px;
    height: 1px;
    background: rgba(0, 203, 171, 1);
  }
}
</style>


